.card-video {
  display: flex;
  flex-direction: column;
  gap: 10px;
  video{
    transition: opacity 0.45s ease; /* Transition de l'opacité */
  }
  width: 100%;
  box-sizing: border-box;
  border-radius: 25%;
}

.card-video:not(:hover) {
    opacity: 0.6; /* Opacité réduite lors du survol */
}
